
.label {
  height: 35px;
  font-size: 13px;
  color: #666666;
  padding-left: 15px;
  line-height: 35px;
  text-align: left;
}
.txtDiv {
  width: 100%;
  height: 45px;
  background: white;
  padding: auto 15px;
}
.txt {
  width: 100%;
  height: 45px;
  font-size: 15px;
  color: #333333;
  background: white;
  /* line-height: 45px; */
  /* display: flex;
  align-items: center; */
  border: none;
  outline: none;
  padding: 0 15px 0 15px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.date {
  height: 80px;
  background: white;
  padding-left: 10px;
  padding-right: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.date span {
  font-size: 15px;
  color: #666666;
  margin: 3px auto;
}
.gap {
  width: 1px;
  background: #eeeeee;
  height: 35px;
}
.item {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -ms-flex-item-align: center;
      align-self: center;
}
.agreeLabel {
  margin: auto 15px;
  text-align: left;
  font-size: 12px;
  vertical-align: middle;
  margin-bottom: 25px;
}
.agreeLabel input {
  outline: none;
  border: none;
}
.item_content {
  width: 100%;
  height: 100%;
  background: transparent;
  position: relative;
}
.item_grid {
  width: 100%;
  height: 100%;
  background: transparent;
  position: absolute;
}
.date-box {
  width: 100%;
  height: 100%;
  background: transparent;
  position: absolute;
}
.date-cover {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  border: none;
  outline: none;
  background: transparent;
  opacity: 0;
}
.routerLink {
  text-decoration: none;
  color: #4080f8;
  font-size: 12px;
}



